.root {
  width: 100%;
  margin: 0 auto;
  max-width: 800px;
  font-family: "Proxima Nova";
  display: flex;
  flex-direction: column;
  padding: 50px;
  align-items: center;
  border-radius: 4px;
  background: rgba(27, 27, 27, 0.93);
  margin-bottom: 50px;
}
.header {
  margin: 50px 0 50px 0;
}
.container {
  width: 100%;
  max-width: 600px;
  height: 100%;
}
.developers {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 30px;
}

.input {
  width: 100%;
  height: 40px;
  margin: 10px 0 50px 0;
  font-size: 20px;
  padding-left: 10px;
  color: black;
}
.input_option {
  color: black;
}
.rate {
  composes: input;
  max-width: 300px;
}
.rate_option {
  color: black;
}
.input_discount {
  composes: input;
  pointer-events: none;
}
.description {
  width: 100%;
  height: 300px;
  margin: 10px 0 50px 0;
  font-size: 16px;
  padding: 10px;
  color: black;
}
.price {
  display: flex;
  gap: 20px;
}
.button_add {
  display: block;
  margin: 0 auto;
  width: 200px;
  height: 50px;
  background-color: #9146ff;
  cursor: pointer;
  border: 0;
  color: white;
  font-weight: 600;
  font-size: 20px;
  border-radius: 5px;
}
.button_add:hover {
  background-color: #522398;
}
.title {
  margin-bottom: 20px;
}
.systemReqNumbers {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (min-width: 800px) {
  .systemReqNumbers {
    flex-direction: row;
  }
}
