.root {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #181818;
}
.root_error {
  width: 100%;
  height: 100vh;
  background: #181818;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.not_found_img {
  width: 50%;
  margin-bottom: 30px;
}
.not_found_message {
  font-size: 30px;
  color: white;
  font-weight: 600;
  font-family: "Proxima Nova";
}
